.Card {
  border-radius: 20px;
  box-sizing: border-box;
  margin: 10px;
  box-shadow: 6px 4px 13px -2px rgba(0,0,0,0.4);
-webkit-box-shadow: 6px 4px 13px -2px rgba(0,0,0,0.4);
-moz-box-shadow: 6px 4px 13px -2px rgba(0,0,0,0.4);
}
.product-box .img-section{
    position: relative;
}


.card-img {
  border: 1px;
  width: 100%;
  height: 70%;
  padding: 35px;
  box-sizing: border-box;
  opacity: 1;
  display: block;
  transition: .5s ease;
  backface-visibility: hidden;
}
.product-box .overlay{
    position: absolute;
    top: 5px;
    bottom: 5px;
    left: 5px;
    right: 5px;
    opacity: 0;
    transition: .3s ease;
    background-color: #404040;
    text-align: center;
    border-radius: 10px;
  }
  .product-box .img-section:hover .overlay{
    cursor: pointer;
    opacity: 0.9;
    transform: scale(0.9,0.9);
    -ms-transform: scale(0.9,0.9); 
    -webkit-transform: scale(0.9,0.9); 
    transition: .5s ease;
 }
 .product-box .img-section .overlay .bton{
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
  }

.card-title{
    font-size: 16px !important;
    line-height: 20px;
    padding-left: 10px;
    padding-right:10px;
}
.card-description{
    font-size: 14px !important;
    padding-left: 10px;
    padding-right: 15px;

}
.margin-Top{
    margin-top: 70px;
}

